.left {
  float: left;
}

.clear {
  clear: both;
}

.form-group {
  height: 4rem;
  position: relative;
  margin-bottom: 2.5rem;
}

.form-control {
  font-size: 1.5rem;
}

.form-control-placeholder {
  position: absolute;
  top: 0;
  padding: 5px 0 0 10px;
  transition: all 200ms;
  opacity: 0.5;
}

.form-control:focus + .form-control-placeholder,
.form-control:valid + .form-control-placeholder {
  font-size: 100%;
  transform: translate3d(0%, -75%, 0);
  opacity: 0.55;
}

.form-control {
  font-size: 1.5rem;
}

.selectform {
  width: 100%;
}

.checkboxpad {
  padding: 10px;
}

/* For date-picker */

#datepicker {
  width: 180px;
  margin: 0 20px 20px 20px;
}
#datepicker > span:hover {
  cursor: pointer;
}
